:root {
  --main-bg-color: #333;
  --main-color: #ffd000;
  --font: "Roboto Mono", monospace;
}

* {
  box-sizing: border-box;
}

html {
  overflow-x: hidden;
}

div {
  display: inline-block;
}

@media (width <= 768px) {
  * {
    -webkit-overflow-scrolling: touch;
  }

  html, body {
    width: 100%;
    margin: auto;
    overflow-x: hidden;
  }
}

body {
  background-color: var(--main-bg-color);
  color: var(--main-color);
  font-family: var(--font);
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
}

@media (width <= 768px) {
  body {
    font-size: 14px;
    line-height: 1.4;
  }
}

#container {
  grid-template: "main main"
                 "controls help"
                 "footer footer"
                 / 50% 50%;
  gap: 3rem 2rem;
  max-width: 60rem;
  margin: 2rem auto;
  padding: 0 1rem;
  display: grid;
}

@media (width <= 768px) {
  #container {
    grid-template-columns: 1fr;
    grid-template-areas: "main"
                         "controls"
                         "help"
                         "footer";
    gap: 2rem 0;
    margin: 1rem auto;
    padding: 0 .5rem;
  }
}

.editor {
  grid-area: main;
}

.waveform-container {
  border: 1px solid var(--main-color);
  width: 100%;
  position: relative;
}

#waveform {
  width: 100%;
  position: relative;
}

#waveform-timeline {
  background-color: var(--main-bg-color);
  width: 100%;
  position: relative;
}

@media (width <= 768px) {
  .waveform-container {
    min-height: 150px;
    margin-bottom: 1rem;
  }

  #waveform {
    min-height: 150px;
  }

  #waveform-load-overlay {
    text-align: center;
    padding: 1rem;
    font-size: 1.5rem !important;
  }
}

.controls {
  grid-area: controls;
}

.help {
  grid-area: help;
}

.footer {
  text-align: center;
  grid-area: footer;
  font-size: .7rem;
}

@media (width <= 768px) {
  .footer {
    padding: 1rem 0;
    font-size: .65rem;
    line-height: 1.3;
  }

  .footer a {
    word-break: break-all;
  }
}

@media (width <= 480px) {
  #container {
    row-gap: 1.5rem;
    margin: .5rem auto;
    padding: 0 .25rem;
  }

  .waveform-container, #waveform {
    min-height: 120px;
  }

  #waveform-load-overlay {
    padding: .5rem;
    font-size: 1.2rem !important;
  }

  button {
    min-height: 40px;
    padding: .5rem;
    font-size: .75rem;
  }

  .algorithm-controls {
    padding: .5rem;
  }

  .slider-container {
    margin-bottom: 1rem;
  }

  .audio-status {
    align-items: right;
    padding: .5rem;
    font-size: .7rem;
  }

  h3 {
    letter-spacing: .1rem;
    padding: .25rem;
    font-size: .8rem;
  }

  .shortcut {
    margin: .5rem 0;
    font-size: .8rem;
  }

  .shortcut-icon {
    width: 2rem;
    height: 2rem;
    margin-right: .5rem;
  }

  .footer {
    padding: .75rem 0;
    font-size: .6rem;
  }
}

.linethrough {
  text-decoration: line-through;
}

h3 {
  text-transform: uppercase;
  letter-spacing: .4rem;
  background-color: var(--main-color);
  color: var(--main-bg-color);
  padding: .4rem;
  font-size: 1rem;
  font-weight: 700;
}

a {
  color: var(--main-color);
}

hr {
  background-color: #ffd0004d;
  border: none;
  height: 1px;
  margin: 1rem 0;
}

@media (width <= 768px) {
  hr {
    margin: .75rem 0;
  }

  a {
    word-break: break-word;
  }
}

.shortcut {
  vertical-align: middle;
  margin: 1rem 0;
}

.shortcut-icon {
  background-color: var(--main-color);
  color: var(--main-bg-color);
  box-sizing: border-box;
  text-align: center;
  vertical-align: middle;
  border: 2px inset #ffffff80;
  border-color: #ffffff80 #ffffff80 #00000080 #00000080;
  width: 2rem;
  height: 2rem;
  margin-right: 1rem;
  display: inline-block;
}

@media (width <= 768px) {
  .shortcut {
    align-items: center;
    margin: .75rem 0;
    font-size: .9rem;
    display: flex;
  }

  .shortcut-icon {
    flex-shrink: 0;
    width: 2.5rem;
    height: 2.5rem;
    margin-right: .75rem;
  }

  .help p {
    margin-bottom: .75rem;
    font-size: .9rem;
    line-height: 1.4;
  }

  h3 {
    letter-spacing: .2rem;
    padding: .3rem;
    font-size: .9rem;
  }
}

button {
  background-color: var(--main-color);
  color: var(--main-bg-color);
  box-sizing: border-box;
  text-align: center;
  vertical-align: middle;
  text-transform: uppercase;
  letter-spacing: .1rem;
  cursor: pointer;
  touch-action: manipulation;
  border: 2px inset #ffffff80;
  border-color: #ffffff80 #ffffff80 #00000080 #00000080;
  margin-bottom: 10px;
  padding: 1rem;
  font-family: Roboto Mono;
  font-size: .9rem;
  display: inline-block;
}

@media (width <= 768px) {
  button {
    width: 100%;
    min-height: 44px;
    margin-bottom: .5rem;
    padding: .75rem;
    font-size: .8rem;
  }

  .export-controls {
    flex-direction: column;
    gap: .5rem;
  }

  .export-controls button, .dropdown {
    width: 100%;
  }

  .dropdown-button {
    width: 100%;
    min-height: 44px;
    padding: .75rem;
  }
}

.grey {
  background-color: #999;
}

.shortcut-icon div {
  vertical-align: middle;
}

#overlay {
  z-index: 9999;
  pointer-events: auto;
  background-color: #000c;
  width: 100vw;
  height: 100vh;
  display: none;
  position: fixed;
  inset: 0;
}

#overlaytext {
  color: var(--main-color);
  text-align: center;
  -webkit-user-select: none;
  user-select: none;
  font-size: 10rem;
  font-weight: bolder;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media (width <= 768px) {
  #overlay {
    background-color: #000000e6;
  }

  #overlaytext {
    word-break: break-word;
    padding: 1rem;
    font-size: 4rem;
    line-height: 1.2;
  }
}

@media (width <= 480px) {
  #overlaytext {
    padding: 2rem;
    font-size: 3rem;
  }
}

#waveform-load-overlay {
  color: #ffd000;
  z-index: 10;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  background: #1e1e1ed9;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 2rem;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

#waveform-load-overlay input[type="file"] {
  display: none;
}

.slice-controls {
  margin-top: 1rem;
}

.slice-controls input {
  background-color: var(--main-color);
  color: var(--main-bg-color);
  border: none;
  width: 4rem;
  margin: 0 .5rem 10px;
  padding: .5rem;
}

@media (width <= 768px) {
  .slice-controls {
    flex-direction: column;
    align-items: stretch;
    gap: .5rem;
    display: flex;
  }

  .slice-controls label {
    margin-bottom: .25rem;
    font-weight: bold;
  }

  .slice-controls input {
    box-sizing: border-box;
    width: 100%;
    min-height: 44px;
    margin: 0 0 .5rem;
    padding: .75rem;
    font-size: 1rem;
  }
}

.algorithm-controls {
  background: #ffffff1a;
  border-radius: 4px;
  margin-bottom: 1rem;
  padding: 1rem;
}

.audio-status {
  background: #ffd0001a;
  border-radius: 5px;
  justify-content: space-between;
  align-items: flex-start;
  gap: 10px;
  width: 100%;
  margin-top: 10px;
  padding: 10px;
  font-family: monospace;
  font-size: .9rem;
  line-height: 1.4;
  display: flex;
}

.audio-status > div {
  color: #ffd000;
}

.audio-status-left {
  flex-direction: column;
  flex: 1;
  gap: 5px;
  display: flex;
}

#zoom-info {
  flex-shrink: 0;
  align-items: center;
  gap: 10px;
  display: flex;
}

#zoom-info label {
  align-items: center;
  gap: 5px;
  margin: 0;
  display: flex;
}

#zoom-level {
  white-space: nowrap;
}

@media (width <= 768px) {
  .algorithm-controls {
    margin-bottom: .75rem;
    padding: .75rem;
  }

  .audio-status {
    flex-direction: column;
    align-items: stretch;
    gap: .75rem;
    margin-top: .75rem;
    padding: .75rem;
    font-size: .8rem;
  }

  .audio-status > div {
    margin: 0;
  }

  .audio-status-left {
    gap: .25rem;
  }

  #zoom-info {
    flex-direction: column;
    align-items: stretch;
    gap: .5rem;
  }

  #zoom-info label {
    flex-direction: column;
    gap: .5rem;
    font-weight: bold;
  }

  #zoom-info label input[type="range"] {
    width: 100%;
  }
}

.slider-container {
  flex-direction: column;
  gap: .5rem;
  margin-bottom: 1.5em;
  display: flex;
}

.slider-controls {
  align-items: center;
  gap: 1rem;
  display: flex;
}

.slider-container label {
  min-width: 120px;
}

.slider-help-text {
  color: #999;
  margin-left: 120px;
  font-size: .8em;
  font-style: italic;
  line-height: 1.2;
}

@media (width <= 768px) {
  .slider-controls {
    flex-direction: column;
    align-items: stretch;
    gap: .5rem;
  }

  .slider-container label {
    min-width: auto;
    font-size: .9rem;
    font-weight: bold;
  }

  .slider-help-text {
    margin-top: .25rem;
    margin-left: 0;
    font-size: .75em;
  }

  input[type="range"] {
    height: 8px;
    margin: .5rem 0;
  }

  input[type="range"]::-webkit-slider-thumb {
    width: 20px;
    height: 20px;
  }

  .slider-value {
    text-align: center;
    background: #ffd0001a;
    border-radius: 3px;
    padding: .25rem;
    font-weight: bold;
  }
}

input[type="range"] {
  appearance: none;
  background: #ddd;
  border-radius: 5px;
  outline: none;
  height: 5px;
}

input[type="range"]::-webkit-slider-thumb {
  appearance: none;
  cursor: pointer;
  background: #ffd000;
  border-radius: 50%;
  width: 15px;
  height: 15px;
}

.wavesurfer-loading {
  pointer-events: none;
  position: relative;
}

.wavesurfer-loading:after {
  content: "Recalculating...";
  color: #666;
  z-index: 9999;
  background: #fffc;
  justify-content: center;
  align-items: center;
  font-size: 1.2em;
  display: flex;
  position: absolute;
  inset: 0;
}

section button {
  margin-bottom: 10px;
}

button:disabled, .slice-controls input:disabled {
  opacity: .5;
  cursor: not-allowed;
  background-color: #999;
}

.message-overlay {
  color: var(--main-color);
  z-index: 1000;
  text-align: center;
  background: #000c;
  border-radius: 4px;
  max-width: 80%;
  padding: 1rem 2rem;
  font-size: .9rem;
  display: none;
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.message-overlay.show {
  opacity: 1;
  display: block;
}

@media (width <= 768px) {
  .message-overlay {
    width: auto;
    max-width: none;
    padding: .75rem 1rem;
    font-size: .8rem;
    top: 10px;
    left: 10px;
    right: 10px;
    transform: none;
  }
}

.wavesurfer-region[data-id="crop-region"] {
  border: 2px solid var(--main-color);
  background: #ffd00033 !important;
}

.wavesurfer-region[data-id="crop-region"]:before {
  content: "CROP REGION";
  background: var(--main-color);
  color: var(--main-bg-color);
  border-radius: 2px;
  padding: 2px 6px;
  font-size: .7rem;
  font-weight: bold;
  position: absolute;
  top: -25px;
  left: 5px;
}

@media (width <= 768px) {
  .wavesurfer-region[data-id="crop-region"], .wavesurfer-region[data-id="fade-in-region"], .wavesurfer-region[data-id="fade-out-region"] {
    border-width: 3px;
    min-width: 10px;
  }

  .wavesurfer-region[data-id="crop-region"]:before, .wavesurfer-region[data-id="fade-in-region"]:before, .wavesurfer-region[data-id="fade-out-region"]:before {
    padding: 1px 4px;
    font-size: .6rem;
    top: -20px;
  }
}

.wavesurfer-region[data-id="fade-in-region"] {
  border: 2px solid #0f0;
  background: #0f03 !important;
}

.wavesurfer-region[data-id="fade-in-region"]:before {
  content: "FADE IN";
  color: var(--main-bg-color);
  background: #0f0;
  border-radius: 2px;
  padding: 2px 6px;
  font-size: .7rem;
  font-weight: bold;
  position: absolute;
  top: -25px;
  left: 5px;
}

.wavesurfer-region[data-id="fade-out-region"] {
  border: 2px solid red;
  background: #f003 !important;
}

.wavesurfer-region[data-id="fade-out-region"]:before {
  content: "FADE OUT";
  color: var(--main-bg-color);
  background: red;
  border-radius: 2px;
  padding: 2px 6px;
  font-size: .7rem;
  font-weight: bold;
  position: absolute;
  top: -25px;
  left: 5px;
}

.button {
  text-align: center;
  position: relative;
}

.button .text-lg {
  text-transform: uppercase;
  letter-spacing: .05rem;
  font-size: 1rem;
  font-weight: bold;
}

.button .text-xs {
  opacity: .8;
  font-size: .7rem;
  font-style: italic;
}

button:disabled .text-lg, button:disabled .text-xs {
  opacity: .5;
}

.audio-status {
  background: #ffd0001a;
  border-radius: 5px;
  margin-top: 10px;
  padding: 10px;
  font-family: monospace;
  font-size: .9rem;
  line-height: 1.4;
}

.audio-status > div {
  color: #ffd000;
  margin: 2px 0;
}

#crop-info {
  color: #ffd000 !important;
}

#fade-in-info {
  color: #0f0 !important;
}

#fade-out-info {
  color: red !important;
}

.export-controls {
  align-items: center;
  gap: 5px;
  display: flex;
}

.dropdown {
  display: inline-block;
  position: relative;
}

.dropdown-button {
  background: var(--main-color);
  color: var(--main-bg-color);
  cursor: pointer;
  text-align: center;
  border: none;
  border-radius: 3px;
  min-width: 35px;
  padding: .5rem;
  font-size: 1rem;
}

.dropdown-button:hover {
  background: #ffd000cc;
}

.dropdown-button:disabled {
  opacity: .5;
  cursor: not-allowed;
}

.dropdown-content {
  background: var(--main-bg-color);
  border: 2px solid var(--main-color);
  z-index: 1000;
  border-radius: 5px;
  min-width: 250px;
  display: none;
  position: absolute;
  top: 100%;
  right: 0;
  box-shadow: 0 4px 8px #0000004d;
}

.dropdown-content.show {
  display: block;
}

.dropdown-item {
  cursor: pointer;
  color: var(--main-color);
  border-bottom: 1px solid #ffd00033;
  padding: .75rem 1rem;
  font-size: .9rem;
  transition: background-color .2s;
}

.dropdown-item:last-child {
  border-bottom: none;
}

.dropdown-item:hover {
  background: #ffd0001a;
}

.dropdown-item.selected {
  background: #ffd00033;
  font-weight: bold;
}

.dropdown-item.selected:before {
  content: "✓ ";
  color: var(--main-color);
}

@media (width <= 768px) {
  .dropdown-content {
